
.Header{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 10vh;
    max-height: 10vh;
    background-color: dimgray;
    display: table;
    overflow: hidden;
  }
  
  a{
    color: white;
  }

  .Header a:hover{
    color: white;
  }
    
  .Header > *{
    display: table-cell;
    table-layout: fixed;
    vertical-align: middle;

  }
  
  .headerButtons{
    text-align:left;
    z-index: 2;
  }
  
  .Titulo{
    z-index: -1;
    color: white;
    text-align: center;
    }
 
  .relleno{
    text-align:left;
    visibility: hidden;
  }

  @media only screen and (max-width:464px){
    .oculto{
      visibility: hidden;
      width: 6%;
      margin-left: 20%;
    }

    .headerButtons{
      width: 5%;
    }

    .botonHome{
      display: none;
    }
    
    .aFlecha{
      margin-left: 20%;
      font-size: 4vh;
    }

    .Titulo{
      width: 75%;
    }  

    .Titulo h1{
      font-size: 3.5vh;
    }


  }

  @media only screen and (min-width:465px) and (max-width:1365px){
    .oculto{
      visibility: hidden;
    }

    .Titulo h1{
      font-size: 4vh;
    }

    .headerButtons{
      width: 15%;
      font-size: 4vh;
    }

    .flechaIzq{
      margin-left: 15% !important;
    }

    #headerButtonId{
    
      margin-left: 10%;
      z-index: 5;
    }

    .Titulo{
      width: 75%;
    }    

    .tituloPrimero{
      display: inline;
    }

    .tituloSegundo{
      display: inline;
    }

  }

  @media only screen and (min-width:1366px){
    .oculto{
      visibility: hidden;
    }
    
    .headerButtons{
      width: 10%;
      font-size: 5vh;
    }

    #headerButtonId{
    
      margin-left: 15%;
      z-index: 5;
    }

    .Titulo{
      width: 80%;
    }

    .Titulo h1{
      font-size: 5vh;
    }

    .tituloPrimero{
      display: inline;
    }

    .tituloSegundo{
      display: inline;
    }

  }