.materiasFrontendDH{
    width: 100%;
    height: 100vh;
    background-color: black;
}

.carouselMateriasFrontend{

    width: 100%;
    padding-top: 10vh;
    height: 85vh;
    
}

.carouselMateriasFrontend ul{

    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.objetoFilaFrontend{
    position: relative;
    width: 90%;
    height: 75vh;
    margin-left: 5%;
    margin-right: 5%;
}

.tarjetaFrontend{

    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.tarjetaFrontend:hover{
    cursor: pointer;
}

.divTituloCardFrontend{
    position: absolute;
    width: 100%;
    text-align: center;
    margin-top: 5%;
}

.tituloCardFrontend{
    color: white;
    font-family: "AvantGarde";
    font-size: 4.5vh;
}

.contenedorImagenFront1{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 100%;
    height: 60%;
}

.materiaFront1, .reactIcon{
    width: 100%;
    height: 100%;
}



.reactIcon{
    filter: grayscale(90%);
}


.htmlIcon{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.cssIcon{
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1;
}

.linkToContainer{
    width: 50px;
    height: 50px;
}

.divWarnIconReact{
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
}

.imgWarnIconReact{
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}



@media only screen and (max-width:464px){

    .tarjetaFrontend{
        width: 90%;
        border: 1vw white solid;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box; 
    }

    .front1Icon{
        width: 55%;
        height: 65%;
    }
    
    .htmlIcon{
        margin-left: 2.5%;
    }
    
    .cssIcon{
        margin-right: 2.5%;
    }

    .imgWarnIconReact{
        width: 75%;
        height: 75%;
    }

}

@media only screen and (min-width:465px) and (max-width:1365px){

    .tarjetaFrontend{
        width: 100%;
        border: 0.5vw white solid;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box; 
    }

    .front1Icon{
        width: 60%;
        height: 65%;
    }
    
    .htmlIcon{
        left: -2%;
    }
    
    .cssIcon{
        left: 43%;
    }

    .imgWarnIconReact{
        width: 85%;
        height: 65%;
    }

}

@media only screen and (min-width:1366px){

    .tarjetaFrontend{
        width: 80%;
        border: 0.3vw white solid;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box; 
    }

    .front1Icon{
        width: 55%;
        height: 65%;
    }
    
    .htmlIcon{
        margin-left: 2.5%;
    }
    
    .cssIcon{
        margin-right: 2.5%;
    }

    .imgWarnIconReact{
        width: 75%;
        height: 75%;
        filter: grayscale(100%);
        transition: 0.5s;
    }

    .tarjetaFrontend:hover > .contenedorImagenFront1 > .divWarnIconReact > .imgWarnIconReact{
        transition: 0.5s;
        filter: grayscale(0%);
    }
}