
  .bm-burger-button{
    position: relative;
    width: 100%;
    height: 100%;
  }

  .bm-item-list{
    padding-left: 1vh;
  }


  .burgerMenuItemDiv{
    height: 5vh;
    margin-bottom: 5px;
  }

  .burgerText{
    font-size: 4.5vh;
    color: white;
    font-weight: normal;
    
  }

.bm-menu nav > *:hover{
  cursor: pointer;
}

  
  
  .disclaimer{
    font-size: 4vh;
  }


  .burgerMenuContainer > div > div{
    width: 100%;
    height: 100%;
  }

.burgerMenuContainer > div > div > div{
  height: 100%;
}

.burgerMenuFooter{
    position: absolute;
    width: 100%;
    height: fit-content;
    bottom: 0;
    margin-bottom: 11vh;
}

.thirdPartyLink{
  color: blue !important;

}

@media only screen and (max-width:464px){

  .burgerMenuContainer{
    position: relative;
    width: 10%;
  }

  .burgerMenuContainer > div{
    width: 75%;
    height: 40%;
    float: right;
    margin-right: 15%;
    
  }

  .burgerMenu{
    width: 100% !important;
    margin-top: 3vh;
    
  }

  .socialMedias{
    font-size: 5.5vh;
    color: white;
    margin-right: 5%;
    margin-bottom: 0.5vh;
  }


}

@media only screen and (min-width:465px) and (max-width:1365px){

  .burgerMenuContainer{
    position: relative;
    width: 10%;
  }

  .burgerMenuContainer > div{
    width: 70%;
    height: 40%;
    float: right;
    margin-right: 15%;
    
  }

  .burgerMenu{
    width: 50% !important;
    margin-top: 3vh;
    
  }

  .socialMedias{
    font-size: 4.5vh;
    color: white;
    margin-right: 5%;
    margin-bottom: 5px;
  }


}

@media only screen and (min-width:1366px){

  .burgerMenuContainer{
    position: relative;
    width: 10%;
  }

  .burgerMenuContainer > div{
    width: 40%;
    height: 50%;
    float: right;
    margin-right: 15%;
    
  }

  .burgerMenu{
    width: 50px;
    margin-top: 2.5vh;
    
  }
  
  .burgerText:hover{
    
    font-size: 4.75vh;
    transition: 0.5s;
  }

  .socialMedias{
    font-size: 4.5vh;
    color: white;
    margin-right: 5%;
    margin-bottom: 5px;
  }

}