.homeMaterias {
    width: 100%;
    height: 100vh;
    background-color: black;
}

.carouselMaterias{
    width: 100%;
    height: 85vh;
    padding-top: 10vh;
}

.carouselMaterias ul{
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.carouselMaterias ul li{
    height: fit-content;
}

img:hover{
    cursor: pointer;
}


.materia{
    height: 75vh;
}

.divTituloCardMateria{
    position: absolute;
    height: fit-content;
    text-align: center;
    width: 100%;    
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.tituloCardMateria{
    color: white;
    position: relative;
    text-align: center;
    vertical-align: middle;
    -webkit-text-stroke: 2px black;
    font-family: "AvantGarde";    
    z-index: 6;
}

@font-face {
    font-family: AvantGarde;
    src: url("../fonts/avant-garde.woff");
}

.materiaTopContainer{
    width: 90%;
    height: 100%;
    margin-left: 5%;
    border: 4px white solid;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}

.materiaTopContainer:hover{
    cursor: pointer;
}

.materiaTopContainer:hover > .divWarnIcon{
    filter: grayscale(0%);
    transition: 0.5s;  
}

.materiaImg{
    height:100%;
    width: 90%;
    margin-left: 5%;
    border: 4px white solid;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; 
}


.divWarnIcon{
    position: absolute;
    margin: auto !important;
    top: 0 !important;
    bottom: 0 !important;
    left: 0 !important;
    right: 0 !important;
    z-index: 2;
}

.imgWarnIcon{
    height: 100% !important;
    width: 100% !important;
    margin-left: 0 !important;

}

.backConstruction{
    filter: grayscale(100%);
    width: 100%;
    height: 100%;
}

.inWorking{
    height: 100%;
}

.underConstructMateria{
    position: absolute;
    z-index: 5;
    width: 90%;
    height: 100%;
    background-color: gray;

}

.underConstruction{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    height: 50%;
}

.stopIcon{
    position: absolute;
    z-index: 1; 
}

.workerIcon{
    position: absolute;
    z-index: 2;
}

.stopIconImg{
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.workerIconImg{
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

@media only screen and (max-width:464px){

    .tituloCardMateria{
        font-size: 6vh;
    }
    
    .divWarnIcon{
        height: 50% !important;
        width: 65% !important;
    }

    .stopIcon{
        width: 60%;
        left: 5%;
        height: 100%;
     
    }
    
    .workerIcon{
        width: 60%;
        right: 5%;
        height: 100%;
    }

}

@media only screen and (min-width:465px) and (max-width:1365px){

    .tituloCardMateria{
        font-size: 7vh;
    }

    .divWarnIcon{
        height: 40% !important;
        width: 80% !important;
    }

    .stopIcon{
        width: 80%;
        right: 31%;
        height: 100%;
     
    }
    
    .workerIcon{
        width: 80%;
        left: 31%;
        height: 100%;
    }

}

@media only screen and (min-width:1366px){

    .tituloCardMateria{
        font-size: 7.5vh;
    }

    .divWarnIcon{
        transition: 0.5s;
        filter: grayscale(100%);
        height: 50% !important;
        width: 60% !important;
    }

    .stopIcon{
        width: 60%;
        left: 5%;
        height: 100%;
     
    }
    
    .workerIcon{
        width: 60%;
        right: 5%;
        height: 100%;
    }

}